.tick2b {
    z-index: 0;
    position: relative;
    right: calc(40px * -0.5);
    right: calc(var(--checkbox-size) * -0.5);
    top: calc(40px * -0.75);
    top: calc(var(--checkbox-size) * -0.75);
    width: calc(40px * 0.35);
    width: calc(var(--checkbox-size) * 0.35);
    height: calc(40px * 0.75);
    height: calc(var(--checkbox-size) * 0.75);
